import React from "react";
import Navbar from "./Navbar";
import '../css/Blog.css';
import SearchIcon from '@mui/icons-material/Search';
import FiberManualRecordIcon from '@mui/icons-material/FiberManualRecord';
import AddIcon from '@mui/icons-material/Add';
import TextsmsRoundedIcon from '@mui/icons-material/TextsmsRounded';
import { Link } from "react-router-dom";
import { useEffect, useState } from "react";
import Zoom from 'react-reveal/Zoom';

const Blog = () => {

    // var user_token = JSON.parse(localStorage.getItem('user'));
    // console.log(user_token)
    // let token = user_token.access
    const [blog, setBlog] = useState([]);

    useEffect(() => {
        const requestOptions = {
            method: 'GET',
            redirect: 'follow',
            headers: {
                'Content-Type': 'application/json',
                Accept: 'application/json',
                // Authorization:
                //     'Bearer ' + token,
            },
        };

        fetch(
            'http://35.90.113.221/view_all_posts/',

            requestOptions,
        ).then(resp => {

            resp.json().then(resp => {
                console.log(resp);
                setBlog(resp);
            });
        });
    }, []);


    return (
        <>

            <div className="container-fluid ">
                <div className="row">
                    <div className="col-sm-12 ">
                        <Navbar />
                        <h4 className="h4profile">Blog</h4>
                        <a href="#" className="dashboard">Dashboard</a>
                        <FiberManualRecordIcon className="dot1" />
                        <a href="#" className="user">Blog</a>
                        <FiberManualRecordIcon className="dot2" />
                        <p className="ui">New Post</p>
                        <Link to="/New_post"> <button className="btn pluspostbutton"><AddIcon className="addicon" />New Post</button></Link>
                        <SearchIcon className="blogsearchicon" /><input className="searchpost" type="Search" placeholder="Search post..."></input>
                        <select className="blogselect">
                            <option>Latest</option>
                            <option>Populer</option>
                            <option>Oldest</option>
                        </select>



                        {/*CARD START */}
                        <div className="fcontainer">
                            {
                                blog.map((item,j) =>
                                    <Zoom key={j}>
                                        <div className='fitem'>
                                            <Link style={{ textDecoration: "none" }}   to={`Blogopen/${item.id}`}>
                                                <img className="blog4img1" src="images\blog\avatar_4.jpg"></img>
                                                <img style={{ width: 311, height: 200 }} src={'http://35.90.113.221' + item.images} className="blog4img2"></img>
                                                <p className="blog4p1">{item.post_name}</p>
                                                <p style={{ color: "black" }} className="blog4p2">{item.tag_name}</p>
                                                <TextsmsRoundedIcon className="blog4commenticon w3-large" /><p className="blog4commenticonp">9.30k</p>
                                            </Link>
                                        </div>
                                    </Zoom>
                                )}
                        </div>




                        {/* <div className="row">
                            {
                                blog.map((item) =>
                                    <Zoom>

                                        <div className="col-sm-3 myblog" >
                                            <Link style={{ textDecoration: "none" }} to="/Blogopen">

                                                <img className="blog4img1" src="images\blog\avatar_4.jpg"></img>
                                                <img style={{ width: 311, height: 200 }} src={item.images} className="blog4img2"></img>
                                                <p className="blog4p1">{item.created_date}</p>
                                                <p style={{ color: "black" }} className="blog4p2">{item.blog_name}</p>
                                                <TextsmsRoundedIcon className="blog4commenticon w3-large" /><p className="blog4commenticonp">9.30k</p>
                                                <RemoveRedEyeRoundedIcon className="blog4eyeicon w3-large" /><p className="blog4eyeiconp">698</p>
                                                <ShareIcon className="blog4shareicon w3-large" /><p className="blog4shareiconp ">2.38k</p>

                                            </Link>
                                        </div>

                                    </Zoom>
                                )}
                        </div> */}
                        {/*CARD END */}





                    </div>
                </div>
            </div>
        </>
    )
}
export default Blog;












