import React from "react";
import SearchIcon from '@mui/icons-material/Search';
import NotificationsIcon from '@mui/icons-material/Notifications';
import GroupIcon from '@mui/icons-material/Group';
import { Link } from "react-router-dom";
import '../css/Navbar.css';
import { useEffect, useState } from "react";
const Navbar = () => {
    var user_token = JSON.parse(localStorage.getItem('user'));
    // console.log(user_token)
    let token = user_token.access
    const [user_profile_pic, setUser_profile_pic] = useState([]);


    useEffect(() => {
        const requestOptions = {
            method: 'GET',
            redirect: 'follow',
            headers: {
                'Content-Type': 'application/json',
                Accept: 'application/json',
                Authorization:
                    'Bearer ' + token,
            },
        };

        fetch(
            'http://35.90.113.221/user_profile_pic/',

            requestOptions,
        ).then(resp => {

            resp.json().then(resp => {
                // console.log(resp);
                setUser_profile_pic(resp);
            });
        });
    }, []);


    return (
        <>
            <div className="container-fluid">
                <div className="row">
                    <div className="col-sm-12" style={{ height: 60 }}>
                        <nav className="navbar navbar-expand-sm  sticky-top">
                            <ul className="navbar-nav">
                                <li className="nav-item">
                                    <a className="nav-link" href="#"><SearchIcon className="searchicon" /></a>
                                </li>

                                <li className="nav-item">
                                    <a className="nav-link" href="#"><img className="usflag" src="\images\us.png" /> </a>
                                </li>
                                <li className="nav-item">
                                    <a className="nav-link" href="#"><GroupIcon className="groupicon" /></a>
                                </li>
                                <li className="nav-item">
                                    <Link to="/Profile">
                                        {user_profile_pic.map((item, i) =>
                                            <div key={i}> <img className="profile" src={'http://35.90.113.221' + item.images} /></div>
                                        )}
                                    </Link>
                                </li>
                            </ul>
                        </nav>
                    </div>
                </div>
            </div>
        </>
    )
}
export default Navbar;